import React from 'react';
import PropTypes from 'prop-types';

class TimelineFooter extends React.Component {
  render() {
    return (
      <div className="timeline-footer">
        {this.props.content}
        {this.props.children}
      </div>
    );
  }
}

TimelineFooter.propTypes = { content: PropTypes.any };
TimelineFooter.defaultProps = { content: '' };

export default TimelineFooter;
